
#footer {
  width: 76%;
  z-index: 100;
  display: flex;
  min-height: 10vh;
  overflow: hidden;
  padding: 5vh 12% 5vh 12%;
  justify-content: space-between;
  background-color: rgba(55,55,55,0.1);
  border-top: 3px solid rgba(25,25,25,0.25);
  filter: drop-shadow(1px 1px 25px rgba(55,55,55,.5));
}

#footer-info {
  width: 50%;
  height: 100%;
}

#footer-info p, a {
  color: white;
  text-align: center;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: calc(12px + 0.5vmin);
  font-weight: bold;
  margin: 2.5vh 0 2.5vh 0;
}

#footer-right {
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

#footer-logo {
  display: flex;
  width: 100%;
  margin: 0 0 5% 20%;
  height: 5vh;
  min-height: 64px;
  background-position: center;
  background-repeat: no-repeat;
}

#footer-refs {
  width: 100%;
  height: 100%;
  padding: 0 0 0 20%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

#footer-refs img {
  width: 10vmin;
  max-width: 48px;
}

@media (max-width: 999px) {
  #footer {
    flex-direction: column;
    width: 100%;
    padding: unset;
  }
  #footer-info {
    width: 100%;
    text-align: center;
  }
  #footer-right {
    width: 100%;
  }
  #footer-logo {
    margin-left: unset;
  }
  #footer-refs {
    padding: 0 25% 0 25%;
    width: 50%;
  }
}