
#edit-panels-top-row {
    display: flex;
    justify-content: space-between;

    width: 80vw;
    min-height: 80vh;
    margin: 5vmin auto;
}

#edit-panel-left, #edit-panel-right {
    height: 33vh;
    padding: 5vmin;
    background-color: rgb(156, 62, 62, 0.133);
}

#edit-panel-left {
    display: flex;
    flex-direction: column;
    width: 60vw;
}

#edit-panel-left>h2, #edit-panel-left>h3, #edit-panel-left>h4, #edit-panel-left>input  {
    margin: 1.25vh 0 1.25vh 0 !important;
    padding: 0 0 0 0 !important;
}

#edit-panel-right {
    display: flex;
    flex-direction: column;
    width: 20vw;
}
